
import { FC, useState } from 'react'
import { expandToSamples, expandToAll } from './core/expand'
import { joinPath } from './utils/path'
import { initQps } from './utils/qps'

const qps = initQps<{
	input: string
}>()

const init = `[Turn on/Switch on/Enable/Activate] [the/] driver['s seat/] fan [mode/].
[Turn/Switch] [the/] driver['s seat/] fan on.`

export const App: FC = () => {
	const [input, setInput] = useState(qps.input ?? init)

	return (
		<>
			<nav className='tabs'>
				<a href={joinPath('/')}>Debug</a>
				<a href={joinPath('/convert')}>Convert</a>
			</nav>
			<main className='container'>
				<div>
					<h2>input</h2>
					<textarea
						className='spaced'
						rows={5}
						cols={70}
						value={input}
						onChange={({ currentTarget: { value } }) => {
							setInput(value)
							qps.input = value
						}}
					/>
					<button
						className='button primary'
						onClick={() => {
							if (
								window.confirm(
									'Reset input to default example?',
								)
							) {
								setInput(init)
								delete qps.input
							}
						}}
					>
						Reset
					</button>
				</div>
				<div>
					<h2>sample variations</h2>
					<div className='pre-wrap'>
						{expandToSamples(input).join('\n')}
					</div>
				</div>
				<div>
					<h2>all variations</h2>
					<div className='pre-wrap'>
						{expandToAll(input).join('\n')}
					</div>
				</div>
			</main>
		</>
	)
}
